import './App.css'
import React from 'react';

import Card from './componentes/layout/Card';

import Area from './componentes/Medidas/area';
import Temperatura from './componentes/Medidas/temeratura';
import Frequencia from './componentes/Medidas/frequencia';
import Consumo from './componentes/Medidas/consumo';
import Massa from './componentes/Medidas/massa';
import Comprimento from './componentes/Medidas/comprimento';


import CaixaTxt from './componentes/layout/Txtcaixa';
import Conteudo from './componentes/Medidas/conteudo';

import Rodape from './componentes/Medidas/rodape';



export default props => (
    
    <div className="App">
        <h1>Conversor Rpido</h1>

        
        <script async custom-element="amp-ad" src="https://cdn.ampproject.org/v0/amp-ad-0.1.js"></script>

        <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-6782906233618334"
     crossorigin="anonymous"></script>
    
        
        <div className="Cards">
    

            <Card color='#238199'>
                <Comprimento></Comprimento>
            </Card>
            
            <Card color="#6ebccf">
                <Area></Area>
            </Card>

            <Card color="#9dd6e4">
                <Temperatura></Temperatura>
            </Card>

            <Card color="#c3e1e9">
                <Frequencia></Frequencia>
            </Card>

            <Card color="#6bcfca">
                <Massa></Massa>
            </Card>
            
            <Card color="#8bd3cf">
                <Consumo></Consumo>
            </Card>


            
            
        </div>

        <div>

            <amp-ad width="100vw" height="320"
                        type="adsense"
                        data-ad-client="ca-pub-6782906233618334"
                        data-ad-slot="9135728882"
                        data-auto-format="rspv"
                        data-full-width="">
                    <div overflow=""></div>
            </amp-ad>
                

        </div>
        
        <CaixaTxt>    
            <Conteudo></Conteudo>
        </CaixaTxt>

         
        <Rodape></Rodape>
         
            
        </div>    
            
            
);       