import React from "react";


import calculartemp from "../scripts/temperatura";


export default class Contador extends React.Component {
    


    
    render(){ 
        return (
                <>
                  <div>
                        <section className="Caixa">
                    
                            <div id="medida">
                                <h2>Conversor De Temperatura</h2>
                            </div>

                            <div id="opc1">
                                <select className="Seletor1" id="temp1" >
                                    <option value="Grau">Grau Celsius</option>
                                    <option value="Fahrenheit">Grau Fahrenhei</option>
                                    <option value="Kelvin">Kelvin</option>
                                </select>
                            </div>

                            <div>
                                <input type="number" id="valtemp" placeholder="Incira o valor"></input>
                            </div>

                            <div id="opc2">
                                <select className="Seletor2" id="temp2" >
                                    <option value="Grau">Grau Celsius</option>
                                    <option value="Fahrenheit">Grau Fahrenhei</option>
                                    <option value="Kelvin">Kelvin</option>
                                </select>  
                            </div>

                            <div>
                                <button className="Botao" onClick={calculartemp}>Calcular</button>
            
                
                            </div>
                    
                            <div className="Resultado" id="restemp">Resultado</div>
                            
                            
                            
                        
                        </section>
                    </div>  
                </>

        )
    }
}