import React from "react";


import calcularfre from "../scripts/frequencia";

export default class Contador extends React.Component {
    


    
    render(){ 
        return (
            <>
                <div>
                    <section className="Caixa">
            
                        <div id="medida">
                            <h2>Conversor De Frequncia</h2>
                        </div>
                        
                        <div id="opc1">
                            <select className="Seletor1" id="fre1" >
                                <option value="Hertz">Hertz</option>
                                <option value="Quilo Hertz">Quilo Hertz</option>
                                <option value="Mega Hertz">Mega Hertz</option>
                                <option value="Giga Hertz">Giga Hertz</option>
                                <option value="rpm">R.P.M</option>
                            </select>
                        </div>
                            
                        <div>
                            <input type="number" id="valfre" placeholder="Incira o valor"></input>
                        </div> 
                        
                        <div id="opc2">
                            <select className="Seletor2" id="fre2" >
                                <option value="Hertz">Hertz</option>
                                <option value="Quilo Hertz">Quilo Hertz</option>
                                <option value="Mega Hertz">Mega Hertz</option>
                                <option value="Giga Hertz">Giga Hertz</option>
                                <option value="rpm">R.P.M</option>
                            </select>
                        </div>
                            
                          
                        <div>
                            <button className="Botao" onClick={calcularfre}>Calcular</button>
                        </div>

                           
                        
                        <div className="Resultado" id="resfre">Resultado</div>
                        
                        
                    </section>
                </div>
            </>
        );
    };
};