import React from "react";

export default 
    function calculartemp() {
        var opc1 = document.getElementById('temp1').value
        var opc2 = document.getElementById('temp2').value
        var res = document.querySelector('div#restemp')
        var valor = document.getElementById('valtemp').value
        // caso opc1 seja Graus
        if (opc1 == 'Grau' && opc2 == 'Fahrenheit'){
            var fa = (valor * 9/5) + 32
            res.innerHTML = `${valor} Graus Celsius so ${fa} Graus Fahrenhait` 
        }else if (opc1 == 'Grau' && opc2 == 'Kelvin'){
            var ke = (valor + 273.15)
            res.innerHTML = `${valor} Graus Celsius so ${ke} Kelvin` 
        }else if (opc1 == 'Grau' && opc2 == 'Grau'){
            var ce = valor 
            res.innerHTML = `${valor} Graus Celsius so ${ce} Graus Celsius`
        }
        // caso opc1 seja Fahrenhait
        if (opc1 == 'Fahrenheit' && opc2 == 'Fahrenheit'){
            var fa = valor
            res.innerHTML = `${valor} Fahrenheit so ${fa} Graus Fahrenhait` 
        }else if (opc1 == 'Fahrenheit' && opc2 == 'Kelvin'){
            var ke = ((valor - 32) * 5/9 + 273.15)
            res.innerHTML = `${valor} Fahrenheit so ${ke} Kelvin` 
        }else if (opc1 == 'Fahrenheit' && opc2 == 'Grau'){
            var ce = (valor - 32)* 5/9 
            res.innerHTML = `${valor} Fahrenheit so ${ce} Graus Celsius`
        }
        // caso opc1 seja Fahrenhait
        if (opc1 == 'Kelvin' && opc2 == 'Fahrenheit'){
            var fa = (valor - 273.15)* 9/5 + 32
            res.innerHTML = `${valor} Kelvin so ${fa} Graus Fahrenhait` 
        }else if (opc1 == 'Kelvin' && opc2 == 'Kelvin'){
            var ke = valor 
            res.innerHTML = `${valor} Kelvin so ${ke} Kelvin` 
        }else if (opc1 == 'Kelvin' && opc2 == 'Grau'){
            var ce = valor - 273.15
            res.innerHTML = `${valor} Kelvin so ${ce} Graus Celsius`
        }

    }