export default
function calcularcom() {
    var opc1 = document.getElementById('com1').value
    var opc2 = document.getElementById('com2').value
    var res = document.querySelector('div#rescom')
    var valor = document.getElementById('valcom').value
    // caso opc1 seja milha po galao americano
    if (opc1 == 'galao americano' && opc2 == 'galao imperial'){
        var gi = valor * 1.201
        res.innerHTML = `${valor} Gales Americano so ${gi} Gales Imperais`
    }else if (opc1 == 'galao americano' && opc2 == 'kmlitro'){
        var kml = valor / 2.352
        res.innerHTML = `${valor} Gales Americano so ${kml} Quilometros por Litro`
    }else if (opc1 == 'galao americano' && opc2 =='litro100km'){
        var lkm = valor * 235.215
        res.innerHTML = `${valor} Gales Americano so ${lkm} Litros por 100 Km`
    }else if(opc1 == 'galao americano' && opc2 =='galao americano'){
        var ga = valor 
        res.innerHTML = `${valor} Gales Americano so ${ga} Gales Americano`
    }
    // caso opc1 seja milha po galao imperial
    if (opc1 == 'galao imperial' && opc2 == 'galao imperial'){
        var gi = valor 
        res.innerHTML = `${valor} Gales Imperiais so ${gi} Gales Imperais`
    }else if (opc1 == 'galao imperial' && opc2 =='kmlitro'){
        var kml = valor / 2.825
        res.innerHTML = `${valor} Gales Imperiais so ${kml} Quilometros por Litro`
    }else if (opc1 == 'galao imperial' && opc2 =='litro100km'){
        var lkm = valor * 282.481
        res.innerHTML = `${valor} Gales Imperiais so ${lkm} Litros Por 100 Km`
    }else if (opc1 == 'galao imperial' && opc2 =='galao americano'){
        var ga = valor / 1.201
        res.innerHTML = `${valor} Gales Imperiais so ${ga} Gales Americano`
    }
    // caso opc1 seja  Quilometro po litro
    if (opc1 == 'kmlitro' && opc2 == 'galao imperial'){
        var gi = valor * 2.825
        res.innerHTML = `${valor} Quilometros por litro so ${gi} Gales Imperais`
    }else if (opc1 == 'kmlitro' && opc2 =='kmlitro'){
        var kml = valor 
        res.innerHTML = `${valor} Quilometros por litro so ${kml} Quilometros por Litro`
    }else if (opc1 == 'kmlitro' && opc2 =='litro100km'){
        var lkm= valor * 100
        res.innerHTML = `${valor} Quilometros por litro so ${lkm} Litros Por 100 Km`
    }else if (opc1 == 'kmlitro' && opc2 =='galao americano'){
        var ga= valor * 2.352
        res.innerHTML = `${valor} Quilometros por litro so ${ga} Gales Americano`
    }
    // caso opc1 seja  litro por 100 quilometros
    if (opc1 == 'litro100km' && opc2 == 'galao imperial'){
        var gi = valor * 282.481
        res.innerHTML = `${valor} Litros por 100 Km so ${gi} Gales Imperais`
    }else if (opc1 == 'litro100km' && opc2 =='kmlitro'){
        var kml = valor * 100
        res.innerHTML = `${valor} Litros por 100 Km so ${kml} Quilometros por Litro`
    }else if (opc1 == 'litro100km' && opc2 =='litro100km'){
        var lkm = valor 
        res.innerHTML = `${valor} Litros por 100 Km so ${lkm} Litro por 100 Km`
    }else if (opc1 == 'litro100km' && opc2 =='galao americano'){
        var ga = valor * 235.215
        res.innerHTML = `${valor} Litros por 100 Km so ${ga} Gales Americano`
    }


}