import React from "react";


import calcularcom from "../scripts/consumo";

export default class Contador extends React.Component {
    


    
    render(){ 
        return (
                <>
                    <div>
                        <section className="Caixa">
        
                            <div id="medida">
                                <h2>Conversor De Consumo De Combustvel</h2>
                            </div>
                            
                            <div id="opc1">
                                <select className="Seletor1" id="com1" >
                                    <option value="galao americano">Milha Por Galo Americano</option>
                                    <option value="galao imperial">Milha Por Galo Imperial</option>
                                    <option value="kmlitro">Quilmetro Por Litro</option>
                                    <option value="litro100km">Litro por 100 Quilmetros</option>
                                </select>
                            </div>
                                
                            <div>
                                <input type="number" id="valcom" placeholder="Incira o valor"></input>
                            </div>
                            
                            <div id="opc2">
                                <select className="Seletor2" id="com2" >
                                    <option value="galao americano">Milha Por Galo Americano</option>
                                    <option value="galao imperial">Milha Por Galo Imperial</option>
                                    <option value="kmlitro">Quilmetro Por Litro</option>
                                    <option value="litro100km">Litro por 100 Quilmetros</option>
                                </select>  
                            </div>
                                
                                
                            <div>
                                <button className="Botao" onClick={calcularcom}>Calcular</button>
                            </div>
                            
                            <div className="Resultado" id="rescom">Resultado</div>
                                
                                
                               
                    
                        </section>
                    </div>
                </>
        );
    };
};