import React from "react";

export default 
    function calcularfre() {
        var opc1 = document.getElementById('fre1').value
        var opc2 = document.getElementById('fre2').value
        var res = document.querySelector('div#resfre')
        var valor = document.getElementById('valfre').value
        // caso opc1 seja Hertz
        if (opc1 == 'Hertz' && opc2 =='Quilo Hertz'){
            var quilo = valor / 1000
            res.innerHTML = `${valor} Hertz so ${quilo} Quilo Hertz`
        }else if (opc1 == 'Hertz' && opc2 == 'Mega Hertz' ){
            var mega = valor / 1e+6
            res.innerHTML = `${valor} Hertz so ${mega} Mega Hertz`
        }else if (opc1 == 'Hertz' && opc2 == 'Giga Hertz'){
            var giga = valor / 1e+9
            res.innerHTML = `${valor} Hertz so ${giga} Giga Hertz`
        }else if (opc1 == 'Hertz' && opc2 == 'Hertz'){
            var hertz = valor
            res.innerHTML = `${valor} Hertz so ${hertz} Hertz`
        }else if (opc1 == 'Hertz' && opc2 =='rpm'){
            var quilo = valor * 60
            res.innerHTML = `${valor} Hertz so ${quilo} R.P.M(Rotaes por minuto)`
        }
        // caso opc1 seja quilo
        if (opc1 == 'Quilo Hertz' && opc2 =='Quilo Hertz'){
            var quilo = valor 
            res.innerHTML = `${valor} Quilo Hertz so ${quilo} Quilo Hertz`
        }else if (opc1 == 'Quilo Hertz' && opc2 == 'Mega Hertz' ){
            var mega = valor / 1000
            res.innerHTML = `${valor} Quilo Hertz so ${mega} Mega Hertz`
        }else if (opc1 == 'Quilo Hertz' && opc2 == 'Giga Hertz'){
            var giga = valor / 1e+6
            res.innerHTML = `${valor} Quilo Hertz so ${giga} Giga Hertz`
        }else if (opc1 == 'Quilo Hertz' && opc2 == 'Hertz'){
            var hertz = valor * 1000
            res.innerHTML = `${valor} Quilo Hertz so ${hertz} Hertz`
        }
        // caso opc1 seja mega
        if (opc1 == 'Mega Hertz' && opc2 =='Quilo Hertz'){
            var quilo = valor * 1000
            res.innerHTML = `${valor} Mega Hertz so ${quilo} Quilo Hertz`
        }else if (opc1 == 'Mega Hertz' && opc2 == 'Mega Hertz' ){
            var mega = valor 
            res.innerHTML = `${valor} Mega Hertz so ${mega} Mega Hertz`
        }else if (opc1 == 'Mega Hertz' && opc2 == 'Giga Hertz'){
            var giga = valor / 1000
            res.innerHTML = `${valor} Mega Hertz so ${giga} Giga Hertz`
        }else if (opc1 == 'Mega Hertz' && opc2 == 'Hertz'){
            var hertz = valor * 1e+6
            res.innerHTML = `${valor} Mega Hertz so ${hertz} Hertz`
        }
        // caso opc1 seja giga
        if (opc1 == 'Giga Hertz' && opc2 =='Quilo Hertz'){
            var quilo = valor * 1e+6
            res.innerHTML = `${valor} Giga Hertz so ${quilo} Quilo Hertz`
        }else if (opc1 == 'Giga Hertz' && opc2 == 'Mega Hertz' ){
            var mega = valor * 1000
            res.innerHTML = `${valor} Giga Hertz so ${mega} Mega Hertz`
        }else if (opc1 == 'Giga Hertz' && opc2 == 'Giga Hertz'){
            var giga = valor 
            res.innerHTML = `${valor} Giga Hertz so ${giga} Giga Hertz`
        }else if (opc1 == 'Giga Hertz' && opc2 == 'Hertz'){
            var hertz = valor * 1e+9
            res.innerHTML = `${valor} Giga Hertz so ${hertz} Hertz`
        }
        // caso opc1 seja rpm
        if (opc1 == 'rpm' && opc2 =='Hertz'){
            var quilo = valor / 60
            res.innerHTML = `${valor} R.P.M so ${quilo} Hertz`
        }




        }