import React from "react";


import calculararea from "../scripts/area";

export default class Contador extends React.Component {
    


    
    render(){ 
        return (
                <>
                    <div>
                        <section className="Caixa">
                    
                    <div id="medida">
                        <h2>Conversor De rea</h2>
                    </div>

                    <div id="opc1">
                        <select className="Seletor1" id="opcao1" >
                            <option value="MetroQ">Metro Quadrado</option>
                            <option value="QuilometroQ">Quilometro Quadrado</option>
                            <option value="MilhaQ">Milha Quadrada</option>
                            <option value="JardaQ">Jarda Quadrada</option>
                            <option value="PeQ">P Quadrado</option>
                            <option value="PolegadaQ">Polegada Quadrada</option>
                            <option value="Hectare">Hectare</option>
                            <option value="Acre">Acre</option>
                        </select>
                    </div>
                    
                    <div>
                        <input type="number" id="ivalor1" placeholder="Incira o valor"></input>
                    </div>
                    
                    <div id="opc2">
                        <select className="Seletor2" id="opcao2" >
                            <option value="MetroQ">Metro Quadrado</option>
                            <option value="QuilometroQ">Quilometro Quadrado</option>
                            <option value="MilhaQ">Milha Quadrada</option>
                            <option value="JardaQ">Jarda Quadrada</option>
                            <option value="PeQ">P Quadrado</option>
                            <option value="PolegadaQ">Polegada Quadrada</option>
                            <option value="Hectare">Hectare</option>
                            <option value="Acre">Acre</option>
                        </select>  
                    </div>

                    <div>
                        <button  className="Botao" onClick={calculararea}>Calcular</button>
                    </div>
                    
                    <div className="Resultado" id="res">Resultado</div>
                        
                
                </section>        
                    
                </div>
            </>        
                    
                        
        );
    };
};