import React from "react";

import calcularmassa from "../scripts/massa";

export default class Contador extends React.Component {
    


    
    render(){ 
        return (
                <>
                    <div>
                        <section className="Caixa">
        
                        <div id="medida">
                            <h2>Conversor De Massa</h2>
                        </div>
                            
                        <div id="opc1">
                                <select className="Seletor1" id="massa1" >
                                    <option value="Tonelada">Tonelada</option>
                                    <option value="Quilograma">Quilograma</option>
                                    <option value="Hectograma">Hectograma</option>
                                    <option value="Decagrama">Decagrama</option>
                                    <option value="Grama">Grama</option>
                                    <option value="Decigrama">Decigrama</option>
                                    <option value="Centigrama">Centigrama</option>
                                    <option value="Miligrama" >Milgrama</option>
                                    <option value="Micrograma" >Micrograma</option>
                                    <option value="Stone">Stone</option>
                                </select>
                        </div>
                                
                        <div>
                            <input type="number" id="valmassa" placeholder="Incira o valor"></input>
                        </div>
                            
                        <div id="opc2">
                                <select className="Seletor2" id="massa2" >
                                    <option value="Tonelada">Tonelada</option>
                                    <option value="Quilograma">Quilograma</option>
                                    <option value="Hectograma">Hectograma</option>
                                    <option value="Decagrama">Decagrama</option>
                                    <option value="Grama">Grama</option>
                                    <option value="Decigrama">Decigrama</option>
                                    <option value="Centigrama">Centigrama</option>
                                    <option value="Miligrama" >Milgrama</option>
                                    <option value="Micrograma" >Micrograma</option>
                                    <option value="Stone">Stone</option>
                                </select>
                        </div>
                                
                        <div>
                            <button className="Botao" onClick={calcularmassa}>Calcular</button>
                        </div>      
                               
                                
                        <div className="Resultado" id="resmassa">Resultado</div>
                                
                                
                                   
                
            
                        </section>
                    </div>
                </>
        );
    };
};